:root {
  --ff-sans: system-ui;
  --ff-mono:
    'San Francisco Mono',
    'Monaco',
    'Consolas',
    'Lucida Console',
    'DejaVu Sans Mono',
    'Bitstream Vera Sans Mono',
    monospace;
  --gray-0: #fafbfc;
  --gray-2: #e1e4e8;
  --gray-4: #959da5;
  --gray-6: #586069;
  --gray-8: #2f363d;
  --gray-10: #1b1f23;
}

/* Fix too low contrast on code */
.cm-s-nord span.cm-comment {
  color: var(--gray-4) !important;
}
